import axios from '@nextcloud/axios';
import Multiselect from '@nextcloud/vue/dist/Components/Multiselect';
import _ from 'lodash';
import { generateUrl, generateOcsUrl } from '@nextcloud/router';
export default {
  name: 'AdminTwoFactor',
  components: {
    Multiselect
  },

  data() {
    return {
      loading: false,
      dirty: false,
      groups: [],
      loadingGroups: false
    };
  },

  computed: {
    enforced: {
      get() {
        return this.$store.state.enforced;
      },

      set(val) {
        this.dirty = true;
        this.$store.commit('setEnforced', val);
      }

    },
    enforcedGroups: {
      get() {
        return this.$store.state.enforcedGroups;
      },

      set(val) {
        this.dirty = true;
        this.$store.commit('setEnforcedGroups', val);
      }

    },
    excludedGroups: {
      get() {
        return this.$store.state.excludedGroups;
      },

      set(val) {
        this.dirty = true;
        this.$store.commit('setExcludedGroups', val);
      }

    }
  },

  mounted() {
    // Groups are loaded dynamically, but the assigned ones *should*
    // be valid groups, so let's add them as initial state
    this.groups = _.sortedUniq(_.uniq(this.enforcedGroups.concat(this.excludedGroups))); // Populate the groups with a first set so the dropdown is not empty
    // when opening the page the first time

    this.searchGroup('');
  },

  methods: {
    searchGroup: _.debounce(function (query) {
      this.loadingGroups = true;
      axios.get(generateOcsUrl('cloud/groups?offset=0&search={query}&limit=20', {
        query
      })).then(res => res.data.ocs).then(ocs => ocs.data.groups).then(groups => {
        this.groups = _.sortedUniq(_.uniq(this.groups.concat(groups)));
      }).catch(err => console.error('could not search groups', err)).then(() => {
        this.loadingGroups = false;
      });
    }, 500),

    saveChanges() {
      this.loading = true;
      const data = {
        enforced: this.enforced,
        enforcedGroups: this.enforcedGroups,
        excludedGroups: this.excludedGroups
      };
      axios.put(generateUrl('/settings/api/admin/twofactorauth'), data).then(resp => resp.data).then(state => {
        this.state = state;
        this.dirty = false;
      }).catch(err => {
        console.error('could not save changes', err);
      }).then(() => {
        this.loading = false;
      });
    }

  }
};